import React from 'react'
import PanelContainer from '../containers/PanelContainer'
import TableResponsive from './TableResponsive'
import CardContainer from '../containers/CardContainer'

export default ({ productionPlanData, onChangeTab }) => {

    return (
        <div className="row">
            <div className="col-sm-12">
                <CardContainer textHeader="Production Plan" cardActions={['reload','toggler']}>
                    <PanelContainer startIndex="1" onChangeTab={onChangeTab} panels={['PM1', 'PM2', 'PM3', 'PM4', 'PM5']}>
                        <TableResponsive {...productionPlanData} />
                    </PanelContainer>
                </CardContainer>
            </div>
        </div>
    )
}


