import React, { Component } from 'react'
import SideBar from '../components/SideBar'



class NavItem {
    constructor({ headText, subItem, path, txt, icon, updateState, clearActiveState, key }) {
        this.headText = headText
        this.subItem = subItem
        this.path = path
        this.txt = txt
        this.icon = icon
        this.clearActiveState = clearActiveState
        this.key = key
        this.updateState = updateState
        if (this.subItem) {
            this.subItem.forEach((item) => {
                item.activeHeadState = () => {
                    this.clearActiveState()
                    this.active = true
                }
            })
        }

    }

    onClick = () => {
        if (this.activeHeadState) {
            this.activeHeadState()
        }
        else if (!this.subItem) {
            this.clearActiveState()
        } else {
            //is head click
            this.updateState()
        }
        this.active = true;
    }
}


export default class SideBarContainer extends Component {

    constructor(props) {
        super(props)
        this.state = {
            sideMenuItem: [
                new NavItem({ key: 1, path: "/", updateState: this.updateState, clearActiveState: this.clearActiveState, txt: "Home", icon: "icon-home" }),
                new NavItem({ key: 2, path: "/DashbordPM", updateState: this.updateState, clearActiveState: this.clearActiveState, txt: "Dashbord PM", icon: "icon-table" }),
                new NavItem({ key: 8, path: "/DashbordMaterail", updateState: this.updateState, clearActiveState: this.clearActiveState, txt: "Dashbord Mat", icon: "icon-table" }),
                new NavItem({ key: 3, path: "/ProductionPlan", updateState: this.updateState, clearActiveState: this.clearActiveState, txt: "ProductionPlan", icon: "icon-calendar" }),
                new NavItem({ key: 4, path: "/ReceivingPlan", updateState: this.updateState, clearActiveState: this.clearActiveState, txt: "ReceivingPlan", icon: "icon-book" }),
                new NavItem({ key: 5, path: "/Inventory", updateState: this.updateState, clearActiveState: this.clearActiveState, txt: "Inventory",icon: "icon-inbox" }),
                new NavItem({ key: 6, path: "/Supplier", updateState: this.updateState, clearActiveState: this.clearActiveState, txt: "Supplier",icon: "icon-truck" }),
                new NavItem({ key: 9, path: "/SupplierDetail", updateState: this.updateState, clearActiveState: this.clearActiveState, txt: "Supplier Detail",icon: "icon-glass" }),
                new NavItem({ key: 7, path: "/Upload", updateState: this.updateState, clearActiveState: this.clearActiveState, txt: "Upload",icon: "icon-upload" }),
                // new NavItem({
                //     key: 5, headText: "Order", updateState: this.updateState, clearActiveState: this.clearActiveState, subItem: [
                //         new NavItem({ key: 6, path: "/Inventory", updateState: this.updateState, clearActiveState: this.clearActiveState, txt: "Inventory" }),
                //         new NavItem({ key: 7, path: "/Supplier", updateState: this.updateState, clearActiveState: this.clearActiveState, txt: "Supplier" }),
                //         new NavItem({ key: 8, path: "/Upload", updateState: this.updateState, clearActiveState: this.clearActiveState, txt: "Upload" }),
                //     ]
                // })
            ]
        }
    }

    updateState = () => {
        this.setState(this.state)
    }

    clearActiveState = () => {
        this.state.sideMenuItem.forEach((item) => {
            item.active = false
            if (item.subItem)
                item.subItem.forEach((itemSub) => {
                    itemSub.active = false
                });
        })
        this.updateState()
    }

    render() {

        let prop = {
            sideMenuItem: this.state.sideMenuItem
        }
        return (
            <SideBar {...prop} />
        )
    }
}


