import React from 'react'

export default ({panels,onChangeTab,children}) => {
    return (
        <div className="panel m-b-lg">
            <ul className="nav nav-tabs nav-justified">
                {
                    panels.map((item) => {
                        return (
                            <li key={item.key} className={item.active ? 'active' : ''}>
                                <a onClick={() => onChangeTab(item.key)}>{item.text}</a>
                            </li>
                        )

                    })
                }
            </ul>
            <div className="tab-content">
                <div className="tab-pane fade active in" id="pm1">
                    <div style={{ overflowX: 'auto' }}>
                        {
                            children
                        }
                    </div>
                </div>
            </div>
        </div>
    )
}


