import React, { Component } from 'react'
import Panel from '../components/Panel'


export default class PanelContainer extends Component {

    constructor(props) {
        super(props)

        let panels = props.panels.map((item, idx) => {
            return {
                key: idx,
                text: item,
                active: !props.startIndex ? (idx === 0 ? true : false) : (props.startIndex == idx ? true : false)
            }
        })

        this.state = {
            panels: panels
        }
    }

    changeTabHandler = (key) => {
        var panels = this.state.panels;
        let selectedPanel = panels.find((item) => {
            return item.key == key
        })

        if (selectedPanel) {
            panels.forEach(element => {
                element.active = false
            })
            selectedPanel.active = true;
            this.props.onChangeTab(key)
        }
    }

    render() {
        return (
            <Panel panels={this.state.panels} onChangeTab={this.changeTabHandler}>
                {this.props.children}
            </Panel>
        )
    }
}


