import React,{Component} from 'react'
import SupplierDetail from '../components/SupplierDetail'

export default class SupplierPage extends Component{

    constructor(props) {
        super(props)
        this.state = {
            supplierData: this.setData()
        }
    }

    setData(key = 0) {
        let rows = []
        let columnHeader = ['Material No.', 'Material Name', 'Suggest']

        for (let i = 0; i < 3; i++) {
            rows.push(['78102000189', '	CHEQUE DYESTUFF', '15'])
        }
        rows.push({datas:['78102000189', '	CHEQUE DYESTUFF', '15'],style:{backgroundColor:'red',color:'white'}})
        rows.push({datas:['78102000189', '	CHEQUE DYESTUFF', '15'],style:{backgroundColor:'yellow',color:'#b0b0b0'}})
        return {
            className: 'table table-striped table-nowarp',
            colunmHeader: columnHeader,
            rows: rows
        }
    }

    render(){
        return (
            <SupplierDetail supplierData={this.state.supplierData}/>
        )
    }
}


