import React from 'react'
import TableResponsive from './TableResponsive'
import CardContainer from '../containers/CardContainer'

export default ({ inventoryData }) => {

    return (
        <div className="row">
            <div className="col-sm-12">
                <CardContainer textHeader="Search Material" number="1" cardActions={['toggler']} bgHeader="bg-primary">
                    <form className="form-horizontal">
                        <div className="form-group col-sm-12 col-md-6">
                            <label for="mat_no" className="col-sm-6 col-md-4">Material No.</label>
                            <div className="col-sm-6 col-md-8">
                                <input type="tel" id="mat_no" className="form-control" />
                            </div>
                        </div>
                        <div className="form-group col-sm-12 col-md-6">
                            <label for="min" className="col-sm-6 col-md-4">Unit less than</label>
                            <div className="col-sm-6 col-md-8">
                                <input type="tel" id="min" className="form-control" />
                            </div>
                        </div>
                        <div className="form-group col-sm-12">
                            <div className="col-sm-12">
                                <button type="button" className="btn btn-primary btn-sm btn-labeled">
                                    <span className="btn-label"><i className="icon icon-search icon-fw icon-lg"></i></span> Search
                        </button>
                            </div>
                        </div>
                    </form>
                </CardContainer>
                <CardContainer textHeader="Search Result" cardActions={['toggler']} bgHeader="bg-primary" number="2">
                    <TableResponsive {...inventoryData} />
                </CardContainer>
            </div>
        </div>
    )
}


