import React,{Component} from 'react'
import ProductionPlan from '../components/ProductionPlan'

export default class ProductionPlanPage extends Component{
    constructor(props) {
        super(props)
        this.state = {
            productionPlan: this.setData()
        }
    }

    onChangeTabHandler = (tabIndex) => {
        this.setState({
            productionPlan: this.setData(tabIndex)
        })
    }

    setData(key=0) {
        let rows = []
        let columnHeader = ['Pmonth','Plant','StationCode','Status','Sequence','PStartDate','Grade','Gram','Speed','Cap','Duration','Ton','DTWH']

        for(let i=0;i<3;i++){
            rows.push([' 2017','TPC','1'+key,'','34192','	19/11/2017 4:32','OSP','70','norm','65','1.55','100.8','24/11/2017'])
        }

        return {
            className: 'table table-striped table-nowarp',
            colunmHeader: columnHeader,
            rows: rows
        }
    }

    render(){
        return (
            <ProductionPlan onChangeTab={this.onChangeTabHandler} productionPlanData={this.state.productionPlan} />
        )
    }
}


