import React from 'react'
import {Route, Switch } from 'react-router-dom'

import DashBordPage from './DashBordPage'
import HomePage from './HomePage'
import InventoryPage from './InventoryPage'
import ProductionPlanPage from './ProductionPlanePage'
import ReceivingPlanPage from './ReceivingPlanPage'
import SupplierPage from './SupplierPage'
import UploadPage from './UploadPage'
import DashbordMaterailPage from './DashBordMaterailPage'
import SupplierDetailPage from './SupplierDetailPage'

export default () => {
    return (
            <Switch>
                <Route exact path="/" component={HomePage} />
                <Route exact path="/Dashbord" component={DashBordPage} />
                <Route exact path="/DashbordMaterail" component={DashbordMaterailPage} />
                <Route exact path="/ProductionPlan" component={ProductionPlanPage} />
                <Route exact path="/ReceivingPlan" component={ReceivingPlanPage} />
                <Route exact path="/Inventory" component={InventoryPage} />
                <Route exact path="/Supplier" component={SupplierPage} />
                <Route exact path="/SupplierDetail" component={SupplierDetailPage} />
                <Route exact path="/Upload" component={UploadPage} />
            </Switch>
    )
}


