import React,{Component} from 'react'
import Inventory from '../components/Inventory'

export default class InventoryPage extends Component{

    constructor(props) {
        super(props)
        this.state = {
            inventoryData: this.setData()
        }
    }

    setData(key = 0) {
        let rows = []
        let columnHeader = ['Material No.', 'KGS/ Unit', 'Inventory', '	Min','MAX','Remark','Unit Price']

        for (let i = 0; i < 3; i++) {
            rows.push(['78102000279', '250/Bag', '38.000', '4.000','10.000','','1200.00'])
        }

        return {
            className: 'table table-striped table-nowarp',
            colunmHeader: columnHeader,
            rows: rows
        }
    }


    render(){
        return (
            <Inventory inventoryData={this.state.inventoryData}/>
        )
    }
}


