import React from 'react'
import PanelContainer from '../containers/PanelContainer'
import TableResponsive from './TableResponsive'

export default ({materailPm,onChangeTab}) => {
    return (
        <PanelContainer onChangeTab={onChangeTab} panels={['PM1','PM2','PM3','PM4','PM5']}>
            <TableResponsive {...materailPm} />
        </PanelContainer>
    )
}


