import React from 'react'
import NavItem from './NavItem'
import NavItemHeader from './NavItemHeader'


export default ({ sideMenuItem }) => {
    return (
        <div className="layout-sidebar">
            <div className="layout-sidebar-backdrop"></div>
            <div className="layout-sidebar-body">
                <nav id="sidenav" className="sidenav-collapse collapse">
                    <ul className="sidenav">
                        <li className="sidenav-heading">MRP Menu</li>
                        {
                            sideMenuItem.map((item) => {
                                if (item.subItem)
                                    return <NavItemHeader {...item} />
                                else
                                    return <NavItem {...item} />
                            })
                        }
                    </ul>
                </nav>
            </div>
        </div>
    )
}


