import React, { Component } from 'react'
import Card from '../components/Card'

export default class CardContainer extends Component {
    constructor(props) {
        super(props)
        this.state = {
            remove: false,
            toggler: false
        }
    }

    toggleEventHandler = (e) => {
        var state = {...this.state}
        state[e]=!state[e]
        this.setState(state,()=>{
            if (e === 'toggler') {
                this.collapsed(this.state[e])
            }
        })
        

    }

    createFadeInPromise = () => {
        return new Promise((reslove) => {
            let sty = this.bodyRef.style
            this.originalHeight = this.bodyRef.clientHeight
            sty.height = `${this.originalHeight}px`
            sty.overflow = 'hidden'
            sty.transition = 'height 0.2s ease-out'
            setTimeout(() => {
                sty.height = '0px'
                setTimeout(() => {
                    sty.display = 'none'
                    reslove()
                }, 160)
            }, 10)
        })
    }

    createFadeOutPromise = () => {
        return new Promise((reslov) => {
            let sty = this.bodyRef.style
            sty.display = 'block'
            setTimeout(() => {
                sty.height = `${this.originalHeight}px`
                setTimeout(() => {
                    sty.overflow = ''
                    reslov()
                }, 200)
            }, 10)
        })
    }


    collapsed = (collaps) => {
        if (collaps) {
            if (this.promise) {
                this.promise =  this.promise.then(this.createFadeInPromise)
            } else {
                this.promise = this.createFadeInPromise()
            }
        } else {
            if (this.promise) {
                this.promise =  this.promise.then(this.createFadeOutPromise)
            } else {
                this.promise = this.createFadeOutPromise()
            }
        }
    }


    render() {
        return (
            <div id="card">
                <Card {...this.props} cardState={this.state} toggleEvent={this.toggleEventHandler} bodyRef={el => this.bodyRef = el}>
                    {this.props.children}
                </Card>
            </div>

        )
    }
}


