import React from 'react'
import TableResponsive from './TableResponsive'
import CardContainer from '../containers/CardContainer'

export default ({ receivingPlanData }) => {

    return (
        <div className="row">
            <div className="col-sm-12">
                <CardContainer textHeader="Material Receiveing Plan" cardActions={['reload', 'toggler']}>

                    <TableResponsive {...receivingPlanData} />

                </CardContainer>
            </div>
        </div>
    )
}


