import React from 'react'
import NavItem from './NavItem'

export default ({ headText, subItem, onClick, active }) => {
    return (
        <li className={`sidenav-item has-subnav ${active ? 'active' : ''}`}>
            <a onClick={onClick} aria-haspopup="true">
                <span className="sidenav-icon icon icon-works">&#103;</span>
                <span className="sidenav-label">{headText}</span>
            </a>
            <ul className="sidenav-subnav collapse" style={{ display: active ? 'block' : 'none' }}>
                {
                    subItem.map(
                        (item) => (
                            <NavItem {...item} />
                        )
                    )
                }
            </ul>
        </li>
    )
}


