import React, { Component } from 'react'
import LayoutPage from './LayoutPage'
import Routes from './routes'
import { BrowserRouter } from 'react-router-dom'

export default class App extends Component {
    render() {
        return (
            <BrowserRouter>
                <LayoutPage>
                    <Routes />
                </LayoutPage>
            </BrowserRouter>
        )

    }
}


