import React from 'react'

export default ({ bgHeader='', textHeader,cardActions=[],children,cardState={},toggleEvent,bodyRef,number}) => {
    var actionTitles = {toggler:'Collapse',reload:'Reload',remove:'Remove'}
    return (
        <div className={`card ${cardState.toggler?'card-collapsed':''}`} style={{display:cardState.remove?'none':'block'}}>
            <div className={`card-header ${bgHeader}`}>
                <div className="card-actions">
                    {
                        cardActions.map((item)=>{
                            return(
                                <button type="button" key={item} className={`card-action card-${item}`} onClick={()=>toggleEvent(item)} title={actionTitles[item]} aria-expanded="true"></button>
                            )
                        })
                    }
                </div>
                <strong>
                    <span className="badge badge-default" style={{display:number?'inline':'none'}}>{number}</span>
                    &nbsp;{textHeader}
                </strong>
            </div>
            <div className="card-body" ref={bodyRef}>
                    {children}
            </div>
        </div>
    )
}


