import React, { Component } from 'react'
import DashBordMaterail from '../components/DashBordMaterail'

export default class DashBordMaterailPage extends Component {

    constructor(props) {
        super(props)
        this.state = {
            materailPm: this.setData()
        }
    }

    onChangeTabHandler = (tabIndex) => {
        this.setState({
            materailPm: this.setData(tabIndex)
        })
    }

    setData(key=0) {
        let rows = ['kb/T']
        let columnHeader = ['Unit']

        for (let i = 0; i < 30; i++) {
            rows.push('G#'+key)
            columnHeader.push(i + 1)
        }

        return {
            className: 'table table-striped table-nowarp',
            colunmHeader: columnHeader,
            rows: [rows]
        }
    }

    render() {
        return (
            <DashBordMaterail materailPm={this.state.materailPm} onChangeTab={this.onChangeTabHandler} />
        )

    }
}


