import React from 'react'

const renderColunm = (row) => {
    return row.map((colunm, idxColunm) => {
        if (typeof colunm === 'string') {
            return <td key={idxColunm}>{colunm}</td>
        } else {
            return <td key={idxColunm} style={colunm.style}>{colunm.text}</td>
        }
    })
}

const renderRow = (row, idx) => {
    if (Array.isArray(row)) {
        return (
            <tr key={idx}>
                {
                    renderColunm(row)
                }
            </tr>
        )
    } else {
        return (
            <tr style={row.style} key={idx}>
                {
                    renderColunm(row.datas)
                }
            </tr>
        )
    }
}

export default ({ className, colunmHeader, rows }) => {
    return (
        <div className="table-response">
            <table className={className}>
                <thead>
                    <tr>
                        {
                            colunmHeader.map((item, idx) => {
                                return <th key={idx}>{item}</th>
                            })
                        }
                    </tr>
                </thead>
                <tbody>
                    {
                        rows.map((row, idx) => {
                            return renderRow(row, idx)
                        })
                    }
                </tbody>
            </table>
        </div>
    )
}


