import React from 'react'
import { Link } from 'react-router-dom'

export default ({path,txt,icon,active,onClick}) =>{  
    return (
        <li className={`sidenav-item ${active?'active':''}`}>
            <Link onClick={onClick} to={path}>
                <span className={`sidenav-icon icon ${icon}`} style={{display:icon?'block':'none'}}></span>
                <span className="sidenav-label">{txt}</span>
            </Link> 
        </li>
    )
}


