import React from 'react'
import SideBarContainer from '../containers/SideBarContainer'

export default (props) => {
    return (
        <div className="layout">
            <div className="layout-header">
                <div className="navbar navbar-default">
                    <div className="navbar-header">
                        <a className="navbar-brand navbar-brand-center" href="/">Elephant Template</a>
                        <button className="navbar-toggler collapsed visible-xs-block" type="button" data-toggle="collapse" data-target="#sidenav">
                            <span className="sr-only">Toggle navigation</span>
                            <span className="bars">
                                <span className="bar-line bar-line-1 out"></span>
                                <span className="bar-line bar-line-2 out"></span>
                                <span className="bar-line bar-line-3 out"></span>
                            </span>
                            <span className="bars bars-x">
                                <span className="bar-line bar-line-4"></span>
                                <span className="bar-line bar-line-5"></span>
                            </span>
                        </button>
                        <button className="navbar-toggler collapsed visible-xs-block" type="button" data-toggle="collapse" data-target="#navbar">
                            <span className="sr-only">Toggle navigation</span>
                            <span className="arrow-up"></span>
                            <span className="ellipsis ellipsis-vertical"></span>
                        </button>
                    </div>
                    <div className="navbar-toggleable">
                        <nav id="navbar" className="navbar-collapse collapse">
                            <button className="sidenav-toggler hidden-xs" title="Collapse sidenav ( [ )" type="button">
                                <span className="sr-only">Toggle navigation</span>
                                <span className="bars">
                                    <span className="bar-line bar-line-1 out"></span>
                                    <span className="bar-line bar-line-2 out"></span>
                                    <span className="bar-line bar-line-3 out"></span>
                                    <span className="bar-line bar-line-4 in"></span>
                                    <span className="bar-line bar-line-5 in"></span>
                                    <span className="bar-line bar-line-6 in"></span>
                                </span>
                            </button>
                            <ul className="nav navbar-nav navbar-right">
                                <li className="visible-xs-block">
                                    <h4 className="navbar-text text-center">Hi, Username</h4>
                                </li>
                                <li className="hidden-xs hidden-sm">
                                    <form className="navbar-search navbar-search-collapsed">
                                        <div className="navbar-search-group">
                                            <input className="navbar-search-input" type="text" placeholder="Search for people, companies, and more" />
                                            <button className="navbar-search-toggler" title="Expand search form ( S )" type="button">
                                                <span className="icon icon-search icon-lg"></span>
                                            </button>
                                            <button className="navbar-search-adv-btn" type="button">Advanced</button>
                                        </div>
                                    </form>
                                </li>
                                <li><a href="#">Link</a></li>
                                <li><a href="#">Link</a></li>
                                <li><a href="#">Link</a></li>
                            </ul>
                        </nav>
                    </div>
                </div>
            </div>
            <div className="layout-main">
                <SideBarContainer/>
                <div className="layout-content">
                    <div className="layout-content-body">
                        <div className="title-bar">
                            <h1 className="title-bar-title">Basic template</h1>
                            <p className="title-bar-description">
                                <small>Use this page as a basic template for developing your application.</small>
                            </p>
                        </div>

                        {props.children}
                    </div>
                </div>
               
            </div>
        </div>
    )
}


